.newPictureForm {
  max-width: 1169px;
  margin: 0 auto;
  font-size: 0.8rem;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-bottom: 10px;
  }

  &__inputFile {
    display: none;
  }

  &__labelFile {
    display: block;
    width: 200px;
    height: 200px;
    border: 1px solid black;
    background-image: url("../../assets/svg/newImg.svg");
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    cursor: pointer;
  }
  &__sizes {
    display: flex;
  }
  &__boxContainer {
    display: flex;
    margin: 0.1rem 0.5rem;
    align-items: center;
    font-size: 30px;
    line-height: 34px;
    & label {
      margin-left: 0.5rem;
    }
    & input {
      width: 30px;
      height: 30px;
    }
  }

  &__checkboxContainer {
    display: flex;
    width: 84%;
    margin-left: 4%;
  }

  &__chooseInclude {
    display: flex;
    flex-direction: column;
  }
  &__photos {
    display: grid;
    grid-template-columns: 10% 90%;
    margin-bottom: 1%;
  }

  &__imgContainer {
    position: relative;
    margin-right: 1%;
  }

  &__images {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  &__btnDelImg {
    position: absolute;
    top: 5px;
    right: 15px;
    display: block;
    width: 40px;
    height: 40px;
    filter: drop-shadow(0px 0px 5px #000000);
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
  }

  &__input {
    border: 1px solid #000000;
    box-sizing: border-box;
    box-shadow: inset 4px 4px 5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 3px 10px;
    outline: none;
    margin-left: 1%;
    font-size: 1rem;
    font-family: "Monotype Corsiva";
    width: 84%;
  }

  &__radioContainer {
    display: flex;
    align-items: center;
    width: 30%;
    cursor: pointer;
    & input {
      width: 29.77px;
      height: 29.77px;
    }
    // & input:checked {
    //   background-color: red;
    //   background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    // }
  }
  &__showIncludes {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-bottom: 10px;
    & span {
      font-size: 48px;
      line-height: 54px;
    }
    & div {
      display: flex;
      justify-content: space-between;
      justify-content: start;
      width: 100%;
      margin-left: 4%;
      & div {
        display: flex;
        align-items: start;
        justify-content: end;
        width: 20%;
        & input {
          margin-left: 8px;
          width: 29.77px;
          height: 29.77px;
        }
      }
    }
  }

  &__labelContainer {
    margin: 0 auto;
    width: 75%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__btnSubmit {
    background: #000000;
    border: 1px solid #000000;
    box-sizing: border-box;
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.25);
    font-size: 1rem;
    font-family: "Monotype Corsiva";
    padding: 2% 5%;
    border-radius: 10px;
    color: #fff;
    outline: none;
    cursor: pointer;

    &:hover {
      background: #353535;
    }
  }
  &__submitContainer {
    text-align: center;
  }
}
