.emblas {
  position: relative;
  background-color: #f7f7f7;
  max-width: 560px;
  margin-left: auto;
  margin-right: auto;
}

.emblas__viewport {
  overflow: hidden;
  width: 100%;
}

.emblas__viewport.is-draggable {
  cursor: move;
  cursor: grab;
}

.emblas__viewport.is-dragging {
  cursor: grabbing;
}

.emblas__container {
  display: flex;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: transparent;
  margin-left: -10px;
}

.emblas__slide {
  position: relative;
  min-width: 100%;
  padding-left: 10px;
}

.emblas__slide__inner {
  position: relative;
  overflow: hidden;
  height: 375px;
  border-radius: 10px;
}

.emblas__slide__img {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: auto;
  min-height: 100%;
  min-width: 100%;
  max-width: none;
  transform: translate(-50%, -50%);
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.emblas__button {
  outline: 0;
  cursor: pointer;
  background-color: transparent;
  touch-action: manipulation;
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  fill: #1bcacd;
  padding: 0;
}

.emblas__button:disabled {
  cursor: default;
  opacity: 0.3;
}

.emblas__button__svg {
  width: 100%;
  height: 100%;
}

.emblas__button--prev {
  left: 27px;
}

.emblas__button--next {
  right: 27px;
}

.emblas--thumb {
  padding-top: 0;
  margin-top: 1%;
}

.embla__container--thumb {
  cursor: default;
  margin-left: -8px;
}

.embla__slide--thumb {
  padding-left: 8px;
  min-width: 20%;
}

.embla__slide__inner--thumb {
  touch-action: manipulation;
  cursor: pointer;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  height: 100px;
  width: 100%;
  background-color: transparent;
  position: relative;
  display: block;
  overflow: hidden;
}

.embla__slide__thumbnail {
  position: absolute;
  opacity: 0.2;
  top: 0;
  bottom: 0;
  left: -10000%;
  right: -10000%;
  margin: auto;
  min-width: 1000%;
  min-height: 1000%;
  max-width: none;
  transform: scale(0.1);
  transition: opacity 0.2s;
}

.embla__slide--thumb.is-selected .embla__slide__thumbnail {
  opacity: 1;
}

@media (max-width: 768px) {
  .emblas__button--prev {
    display: none;
    left: 27px;
  }
  .emblas__button--next {
    display: none;
    right: 27px;
  }
}
