.embla {
  position: relative;
  background-color: #f7f7f7;
  max-width: 560px;
  margin-left: auto;
  margin-right: auto;

  &--svg {
    position: absolute;
    left: 580px;
    top: 20px;
    cursor: pointer;
    margin-left: auto;
    background: black;
    opacity: 0.95;
  }
  &--width {
    max-width: 900px;
  }
}

.embla__viewport {
  overflow: hidden;
  width: 100%;
}

.embla__viewport.is-draggable {
  cursor: move;
  cursor: grab;
  border-radius: 10px;
}

.embla__viewport.is-dragging {
  cursor: grabbing;
  // border-radius: 10px;
}

.embla__container {
  display: flex;
  width: 600px;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: transparent;
  margin-left: -10px;
}

.embla__slide {
  position: relative;
  min-width: 100%;
  padding-left: 10px;
}

.embla__slide__inner {
  position: relative;
  overflow: hidden;
  height: 375px;

  &--height {
    height: 568px;
    border-radius: 10px;
  }
}

.embla__slide__img {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: 520px;
  height: 520px;
  min-height: 100%;
  min-width: 100%;
  max-width: none;
  transform: translate(-50%, -50%);
}

.embla__button {
  outline: 0;
  cursor: pointer;
  background-color: transparent;
  touch-action: manipulation;
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  fill: #1bcacd;
  padding: 0;
}

.embla__button:disabled {
  cursor: default;
  opacity: 0.3;
}

.embla__button__svg {
  width: 100%;
  height: 100%;
}

.embla__button--prev {
  left: 27px;
}

.embla__button--next {
  right: 27px;
}

.embla--thumb {
  padding-top: 0;
  margin-top: 1%;
}

.embla__container--thumb {
  cursor: default;
  margin-left: -8px;
}

.embla__slide--thumb {
  padding-left: 8px;
  min-width: 35%;
}

